import { useState } from 'react';
import styles from './CreateCategoryPopup.module.css';
import ColorList from '../ColorList/ColorList';
import uuid from 'react-uuid';

function CreateCategoryPopup({
  isCreateCategoryPopupOpen,
  closeCreateCategoryPopup,
  createCategory,
  id,
}) {
  const [inputValue, setInputValue] = useState('');
  const [color, setColor] = useState('');

  function handleInput(e) {
    validateInputs();
    setInputValue(e.target.value);
  }

  function chooseColor(e) {
    setColor(e.target.value);
  }

  function handleSubmit(e) {
    e.preventDefault();
    createCategory({
      id: uuid(),
      categoryName: inputValue,
      color,
    });

    closeCreateCategoryPopup();
  }

  function validateInputs(e) {
    // const name = e.target.name;
    // const error = {}

    // console.log('name:', name)
  }

  return (
    <div className={`${isCreateCategoryPopupOpen && styles.popupIsOpen} ${styles.popup}`}>
      <div className={styles.container}>
        <button
          className={styles.closeButton}
          type='button'
          onClick={closeCreateCategoryPopup}
          aria-label='Закрыть попап'
        ></button>
        <form className={styles.form} onSubmit={handleSubmit}>
          <input
            name='color'
            type='text'
            className={styles.input}
            placeholder='Название категории'
            defaultValue=''
            onChange={handleInput}
            required
          />
          <span className={styles.validationError}>Текст ошибки валидации</span>
          <ColorList chooseColor={chooseColor} id={id} />
          <button className={styles.addColorButton} type='submit'>
            Добавить
          </button>
        </form>
      </div>
    </div>
  );
}

export default CreateCategoryPopup;
