import styles from './ColorItem.module.css';

function ColorItem({ color, chooseColor, id }) {
  return (
    <li>
      <input
        type='radio'
        className={`${styles.radio} ${styles[color]}`}
        id={id}
        name='colorList'
        value={color}
        onChange={chooseColor}
        required
      />
      <label htmlFor={id} />
    </li>
  );
}

export default ColorItem;
